<template>	
<section class="wrap has_header_wrap c_wrap">
	<!-- 头部标题 -->
	<com-header titles="同意处方购买协议">
		<template  slot="style-def"></template>
	</com-header>
	<!-- 头部标题 end -->

	<!-- 协议内容 -->
	<div class="con">
		<p>处方药主要包括抗生素、部分治疗高血压的药、部分感冒发烧药以及治疗精神疾病的药物等。根据处方药与非处方药的分类管理办法，处方药必须凭执业医师或执业助理医师处方调配、购买和使用。为保障患者更安全、更有效的用药， 请在购买处方药并提交订单时主动、及时地上传处方信息和登记购买用户基本信息，不得弄虚作假。</p>
	</div>
	<!-- 协议内容 end -->

</section>
</template>

<script>
import comHeaderTitle from '@/components/Common/ComHeaderTitle'

export default {
	name: 'info',
	data (){
		return {

		}
	},
	components: {
		comHeaderTitle, // 公共头部标题
	},
	filters: {
		
	},
	created (){

	},
	mounted (){
		this.$emit('changeClass',false);
	},
	destroyed (){
		this.$emit('changeClass',true);
	},
	methods: {

	},
}	
</script>

<style scoped>
@import '../../../static/css/headerTitle.css';
@import '../../../static/css/childre_wrap.css';

.con {
	padding: 0.3125rem;
}
.con p {
    font-size: 0.375rem;
    line-height: 0.75rem;
    margin-bottom: 0.75rem;
    padding: 0 0.375rem;
    text-indent: 2em;
}
</style>


